'use client'; 

import React, { useState } from 'react';
import { useRouter } from 'next/navigation'; 
import { FaEye, FaEyeSlash } from 'react-icons/fa';

const Login = () => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');
    const [passwordVisible, setPasswordVisible] = useState(false); 
    const router = useRouter(); 
    const handleLogin = async (e: React.FormEvent) => {
        e.preventDefault();
        try {
            const res = await login
            router.push('/dashboard'); 
        } catch (err) {
            setError('Login failed!');
        }
    };

    const togglePasswordVisibility = () => {
        setPasswordVisible(!passwordVisible);
    };

    return (
        <div className="flex justify-center items-center min-h-screen bg-[rgb(243,244,246)]">
            <div className="flex flex-col lg:flex-row w-full max-w-4xl bg-white rounded-xl shadow-2xl overflow-hidden">
                {/* Left side with logo and description */}
                <div className="w-full lg:w-1/2 p-8 flex flex-col justify-center items-center">
                    <img
                        src="/logo.png"
                        alt="Logo"
                        className="mb-6 max-w-[350px] lg:max-w-[400px]" 
                    />
                </div>

                <div className="w-full lg:w-1/2 p-8">
                    <h2 className="text-3xl font-semibold text-gray-800 mb-6 text-center">Login</h2>
                    <form onSubmit={handleLogin} className="space-y-4">
                        <div>
                            <label htmlFor="email" className="block text-sm font-medium text-gray-700">Email</label>
                            <input
                                type="email"
                                id="email"
                                placeholder="Enter your email"
                                className="w-full px-4 py-2 mt-2 text-lg border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500 text-sm"
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}
                                required
                            />
                        </div>
                        <div className="relative">
                            <label htmlFor="password" className="block text-sm font-medium text-gray-700">Password</label>
                            <input
                                type={passwordVisible ? 'text' : 'password'} 
                                id="password"
                                placeholder="Enter your password"
                                className="w-full px-4 py-2 mt-2 text-lg border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500 text-sm"
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                                required
                            />
                            {password && (
                                <button
                                    type="button"
                                    onClick={togglePasswordVisibility}
                                    className="absolute right-4 bottom-1 transform -translate-y-1/2 text-gray-600"
                                >
                                    {passwordVisible ? <FaEyeSlash /> : <FaEye />}
                                </button>
                            )}
                        </div>
                        {error && <p className="text-red-500 text-sm mt-2">{error}</p>}
                        <button
                            type="submit"
                            className="w-full py-3 mt-6 bg-indigo-600 text-white font-semibold rounded-lg shadow-md hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                        >
                            Login
                        </button>
                    </form>

                    <div className="text-center text-sm text-gray-600 mt-4">
                        <a href="/forgot-password" className="text-indigo-600 hover:text-indigo-700">Forgot password?</a>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Login;
